<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">推荐会员</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list">
              <i class="icon"></i>
              <div class="detail">
                <div class="name">
                  <label>{{item.realName}}</label>(<span>{{item.code}}</span>)
                </div>
                <p class="mobile">{{item.mobile}}</p>
              </div>
              <div class="level">
                <label>推荐等级：</label>
                <span>{{item.level}}</span>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {getIRecommendList} from 'api/mine'
  import {ERR_CODE} from 'api/config'

  export default {
    data () {
      return {
        list: []
      }
    },
    created () {
      this._getIRecommendList()
    },
    methods: {
      back() {
        this.$router.back()
      },
      _getIRecommendList() {
        getIRecommendList().then((res) => {
          if(res.errCode === ERR_CODE){
            this.list = res.data
          }
        })
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        width 100%
        height 100%
        overflow hidden
        margin-top 10px
        .list
          background #fff
          .item
            display flex
            align-items center
            height 82px
            border-bottom 1px solid #E6E6E6
            box-sizing border-box
            margin-right 14px
            margin-left 18px
            &:last-child
              border-bottom none
            .icon
              width 39px
              height 36px
              background url("~common/image/mine/star.png") no-repeat
              background-size contain
              margin-right 24px
            .detail
              display flex
              flex 1
              flex-direction column
              .name
                font-size $font-size-medium-m
                color #333
                font-weight bold
              .mobile
                font-size $font-size-small-x
                color #666
                margin-top 16px
            .level
              width 100px
              min-width 100px
              font-size $font-size-small-x
              display flex
              label
                color #666
              span
                color #000
                font-weight bold

</style>



